@import url('https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap');

a{
    color: black !important;
}
.brand-logo,h2{
    font-family: 'Grand Hotel', cursive;
}

.auth-card{
    padding: 20px;
    text-align: center;
    max-width: 400px;
    margin: 10px auto;
}
.mycard{
    margin-top: 30px;
}
.input-field input:focus {
    border-bottom: 1px solid #373add !important;
    box-shadow: 0 1px 0 0 #373add !important;
  }
.gallery{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.item{
    width: 30%;
    height: 30%;
    border-radius: 12px;
}
.home-card{
    max-width: 500px;
    height: max-content;
    margin: 26px auto;
}
#toast-container{
    top: 50px !important;
    right: 20px !important;
    left: auto !important;
}
.material-icons:hover{
    cursor: pointer;
}
.collection-item{
    width: 100%;
    color: black;
}
